<template>
  <div class="page-container">
    <div class="page-box accent">
      <div class="page-box-title">
        <h2>
          <base-icon
            iconRef="people"
            position="left"
          />Klanten
        </h2>
        <div class="flex-divider"></div>
        <base-button
          @click="openCreateModal"
          class="small-on-mobile"
        >
          <base-icon
            iconRef="plus"
            position="left"
          /><span class="button-label">Nieuwe Klant</span>
        </base-button>
      </div>
      <div class="page-box-datatable-container">
        <clients-data-list
          :dataList="clientsList"
          @click="dataEntryClick($event)"
        />
      </div>
    </div>
  </div>
</template>

<script>
import clientsDataList from "./data-list-components/clients-data-list.vue";

export default {
  name: "Clients",
  components: {
    clientsDataList
  },
  computed: {
    clientsList: function() {
      return this.$store.getters.getClientsList.filter(client => {
        return client.data().trashed == false;
      });
    }
  },
  methods: {
    openCreateModal: function() {
      this.$store.commit("setModalShown", {
        dataType: "client",
        modalType: "create",
        shown: true
      });
    },
    dataEntryClick(value) {
      this.openClient(value);
    },
    openClient(targetClientId) {
      this.$router.push(`/klant/${targetClientId}`);
    }
  },
  created: function() {
    this.$setPageTitle("Klanten");
  }
};
</script>

<style>
</style>



